import { Box, Button, Grid } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement, useState } from "react";
import { IProcessTemplate } from "../../../../estreeh-common-library/build/interfaces/IProcessTemplate";
import Branding from "../../config/Branding";
import { Regions } from "../../config/Regions";
import { ProcessTemplateFactory } from "../../factories/ProcessTemplateFactory";
import PageTitle from "../common/PageTitle";
import SubmitButton from "../forms/SubmitButton";
import ProcessTemplate from "./ProcessTemplate";

interface Props extends RouteComponentProps {}

export default function AgencyProcessTemplatesPage(props: Props): ReactElement {
  /* ----------------------------------------
  TODO: IDEAS to be in this page:

  fees Templates management:
  --- Title
  --- Process Fees
  --- Insurance Fees
  --- Accommodation On Arrival
  --- LMRA Fees (Or global???? Display?)
  --- Work Permit (Global??? Display?)
  --- VAT (Display only)
  --- Other fees
  --- Other fees' Dropdown

  -----------------------------------------*/

  const makeBlankProcess = (): IProcessTemplate => {
    return ProcessTemplateFactory.build({
      accommodationFeesBhd: 0,
      insuranceFeesBhd: 0,
      otherFeesBhd: 0,
      processFeesBhd: 0,
      processDurationMaximumMonths: 0,
      processDurationMinimumMonths: 0,
      title: "",
    });
  };
  const fakeProcesses: IProcessTemplate[] = ProcessTemplateFactory.buildList(2);

  const [processes, setProcesses] = useState([...fakeProcesses, makeBlankProcess()]);

  const addClicked = () => {
    setProcesses((prev) => [
      ...prev,
      makeBlankProcess()
    ]);
  };

  return (
    <Box>
      <PageTitle title="Process Templates" />
      <Box>
        {processes.map((process, index) => (
          <Box
            boxShadow={Branding.shadow.niceShadow}
            borderRadius={Branding.borderRadius.niceRadius}
            border={"1px solid " + Branding.light}
            p={1}
            my={1}
          >
            <Grid container spacing={2} alignItems="center">
              <Grid item md={10} xs={12}>
                <ProcessTemplate
                  process={process}
                  region={Regions.bahrain}
                />
              </Grid>
              <Grid item md={2} xs={12}>
                <Button color="primary" variant="outlined">
                  Save
                </Button>
                {/* <Button color="primary" variant="outlined">
                  Delete
                </Button> */}
              </Grid>
            </Grid>
          </Box>
        ))}
      </Box>
      <Box>
        <Button color="primary" variant="contained" onClick={addClicked}>
          + Add More
        </Button>
      </Box>
      <SubmitButton title="Save and Exit" navigate={props.navigate} />
    </Box>
  );
}
