import { Box } from "@material-ui/core";
import React, { ReactElement } from "react";
import { IProcessTemplate } from "../../../../estreeh-common-library/build/interfaces/IProcessTemplate";
import { IRegionConfiguration } from "../../../../estreeh-common-library/build/interfaces/IRegionConfiguration";
import { IBackendField } from "../../../../estreeh-common-library/src/interfaces/IBackendField";
import BackendFieldSet from "../forms/BackendFieldSet";

interface Props {
  process: IProcessTemplate;
  region: IRegionConfiguration;
}

export default function ProcessTemplate(props: Props): ReactElement {
  const p = props.process;
  const currencyCodeSuffix = props.region.currencyCode;

  const fields: IBackendField[] = [
    {
      name: "title",
      defaultValue: p.title,
    },
  ];

  const fieldsFees1: IBackendField[] = [
    {
      name: "processFees",
      defaultValue: p.processFeesBhd > 0 ? p.processFeesBhd : undefined,
      suffix: currencyCodeSuffix,
      type: "number",
    },
    {
      name: "insuranceFees",
      defaultValue: p.insuranceFeesBhd > 0 ? p.insuranceFeesBhd : undefined,
      suffix: currencyCodeSuffix,
      type: "number",
    },
    {
      name: "accommodationFees",
      defaultValue:
        p.accommodationFeesBhd > 0 ? p.accommodationFeesBhd : undefined,
      suffix: currencyCodeSuffix,
      type: "number",
    },
  ];

  const fieldsFeesOther: IBackendField[] = [
    {
      name: "othersName",
      defaultValue: p.otherFeesLabel,
      variant: "select",
      md: 6,
      selectOptions: [
        {
          value: "Others",
        },
        {
          value: "Travel Insurance & Training",
        },
      ],
    },
    {
      name: "othersFees",
      defaultValue: p.otherFeesBhd > 0 ? p.otherFeesBhd : undefined,
      suffix: currencyCodeSuffix,
    },
  ];

  const fieldsDurations: IBackendField[] = [
    {
      name: "minimumMonths",
      label: "Minimum",
      type: "number",
      suffix: "Months",
      defaultValue:
        p.processDurationMinimumMonths > 0
          ? p.processDurationMinimumMonths
          : undefined,
    },
    {
      name: "maximumMonths",
      label: "Maximum",
      type: "number",
      suffix: "Months",
      defaultValue:
        p.processDurationMaximumMonths > 0
          ? p.processDurationMaximumMonths
          : undefined,
    },
  ];

  return (
    <Box px={1}>
      <BackendFieldSet fields={fields} md={6} headerVariant="small" />
      <BackendFieldSet
        header="Fees"
        fields={fieldsFees1}
        md={3}
        headerVariant="small"
      />
      <BackendFieldSet fields={fieldsFeesOther} md={3} headerVariant="small" />
      <BackendFieldSet
        header="Durations"
        fields={fieldsDurations}
        md={3}
        headerVariant="small"
      />
    </Box>
  );
}
