import { Box, Grid, Typography } from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";
import { CSSProperties } from "@material-ui/styles";
import React, { ReactElement } from "react";
import Branding from "../../config/Branding";

interface Props {
  title?:string;
}

export default function DashboardCard(props: Props): ReactElement {
  const styles: CSSProperties = {
    marginLeft: "auto",
    marginRight: "auto",
  };
  return (
    <Grid item md={3} xs={12}>
      <Box bgcolor={Branding.lighter} p={4} borderRadius={Branding.borderRadius.niceRadius} boxShadow={Branding.shadow.niceShadow} textAlign="center">
        <Box pb={2}>
          <Typography style={{ textTransform:"uppercase" }}>
            {props.title}
          </Typography>
        </Box>
        <Box pb={2}>
        <Skeleton
          variant="circle"
          width={100}
          height={100}
          animation="wave"
          style={styles}
        />
        </Box>
        <Box >
        <Skeleton variant="text" animation="wave" style={styles}  />
        </Box>
      </Box>
    </Grid>
  );
}
